import React, { useState } from 'react';
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import {Link} from 'gatsby';
import Constants from '../utils/constants';

const PremintPage = () => { 
    const [isLoading, setIsLoading] = useState(true);
    const onIFrameLoad = () => {
        setIsLoading(false);
    };

    const PageLayout = <Layout pageTitle="Non Fungible Cup: Whitelist" container="inner-page">
        <InnerPageTitle title="Enter in whitelist raffle" subtitle="Add your wallet address and twitter info to get a spot in the C-Shirts whitelist" />
        <div className={isLoading ? "row" : "hide"}>
            <div className="col-md-12 center">
                Loading ...
            </div>
        </div>
        <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSfkfFbCMVSwxNl8-6AUObJMUugO09puKGBswEHDqxHcQMvDIA/viewform?embedded=true" width="100%" height="780" onLoad={onIFrameLoad}>Loading...</iframe>
    </Layout>;
    return (PageLayout);
};

export default PremintPage;